@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media ( prefers-reduced-motion : no-preference ) {

  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  align-items: center;
  background-color: #282c34;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {

  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {

  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.container {
  position: relative;
  width: 350px;
  height: 500px;
  border-radius: 20px;
  padding: 40px;
  box-sizing: border-box;
  background: #ecf0f3;
  box-shadow: 14px 14px 20px #cbced1, -14px -14px 20px white;
  margin: auto;
  margin-top: 100px;
}

.brand-logo {
  height: 100px;
  width: 100px;
  margin: auto;
  border-radius: 50%;
  box-sizing: border-box;
  box-shadow: 7px 7px 10px #cbced1, -7px -7px 10px white;
}

.input-login1 {
  background: #ecf0f3;
  padding: 10px;
  padding-left: 20px;
  height: 20px;
  font-size: 14px;
  border-radius: 50px;
  box-shadow: inset 6px 6px 6px #cbced1, inset -6px -6px 6px white;
  width: 176px;
  margin-left: 20px
}

.input-login2 {
  background: #ecf0f3;
  padding: 10px;
  padding-left: 20px;
  height: 20px;
  font-size: 14px;
  border-radius: 50px;
  box-shadow: inset 6px 6px 6px #cbced1, inset -6px -6px 6px white;
  width: 176px;
  margin-left: 30px
}


.inputs {
  text-align: center;
  margin-top: 30px;
}

.email-text, .password-text {
  text-align: left;
  margin-left: 31px;
  margin-bottom: 5px;
  margin-top: 5px;
}

.btn-login {
  margin-top: 40px;
  background: #2FC18C;
  height: 40px;
  border-radius: 20px;
  cursor: pointer;
  font-weight: 900;
  box-shadow: 6px 6px 6px #cbced1, -6px -6px 6px white;
  transition: 0.5s;
  border: none;
  outline:none;
  width:170px;
  margin-left: 50px;
}

label {
  margin-bottom: 20px;
}

.brand-title {
  margin-top: 10px;
  font-weight: 900;
  font-size: 1.8rem;
  color: #2FC18C;
  letter-spacing: 1px;
  margin-left: 23px;
}

.link {
  font-size: 10px;
  margin-left: 65px;
}
.tasks1 {
  display: flex;
  flex-basis: auto;
  justify-content: space-between;
  background-color: #036B52;
  padding-top: 5px;
  padding-bottom: 5px;
  color: white;
  margin: 10px;
  border-radius: 10px
}

.parag-email {
  margin-right: auto;
  margin-left: 5px;
}
.total-expense {
  margin-right: 10px;
}

.name-trybe {
  margin-right: 450px;
  font-weight: 900;
  font-size: 18px;
  color: #2FC18C;
  letter-spacing: 1px;
}

#btn {
  margin-top: 1px;
  margin-right: 5px;
  background: #2FC18C;
  border-radius: 20px;
  cursor: pointer;
  font-weight: 200;
  transition: 0.5s;
  border: none;
  outline:none;
  width:150px;
}

form {
  background-color: rgb(207, 206, 206);
  padding-top: 10px;
  padding-bottom: 10px;
  margin: 10px;
  border-radius: 10px
}

#value {
  margin-left: 5px;
  margin-right: 10px;
}

#description {
  margin-left: 5px;
  margin-right: 10px;
}

label {
  margin-right: 40px;
  margin-left: 10px;
}

#currency {
  margin-left: 5px;
  margin-right: 10px;
}

#method {
  margin-left: 5px;
  margin-right: 10px;
}

#tag {
  margin-left: 5px;
  margin-right: 10px;
}

