table {
  position:relative;
  border-spacing: 0px;
  margin: 10px;
  border-radius: 10px;
}

th {
  width: 200px;
  border-collapse: collapse;
  font-size: 14px;
  padding-bottom: 5px;
  padding-top: 5px;
}

td {
  text-align: center;
  border-collapse: collapse;
  margin: none;
  font-size: 13px;
  margin: 10px;
}

#pic {
  border-radius: 50%;
  margin-left: 5px;
}

.edit-th {
  border-right: 10px;
}

.edit-button {
  margin-right: 20px;
}

